import React from 'react'

const Button = ({ link, href, target }) => {
  return (
    <button className="outline-none block mx-auto my-4">
      <a
        href={href}
        target={target ? target : '_self'}
        rel="noreferrer"
        className="block w-full h-full rounded-lg bg-yellow-400 text-gray-900 px-4 py-2 uppercase hover:opacity-80"
      >
        {link}
      </a>
    </button>
  )
}

export default Button
